<script>
export default {
    name:'hotspot',
    data(){
        return{
            urid:'',
            keyword:'',
            refresh:true,
            spots:[
                {url:'',title:'最受感触的一个获奖感言1'},
                {url:'',title:'少年派2'},
                {url:'',title:'我的区长父亲3'},
                {url:'',title:'镜头下的紫藤萝瀑布4'},
                {url:'',title:'如果你来读一首诗，你会为谁而读5'},
                {url:'',title:'返校后的第一份早餐6'},
                {url:'',title:'大学生创业项目7'},
                {url:'',title:'马冬梅8'},
                {url:'',title:'NM$L9'},
                {url:'',title:'最受感触的一个获奖感言10'},
                {url:'',title:'少年派11'},
                {url:'',title:'我的区长父亲12'},
                {url:'',title:'镜头下的紫藤萝瀑布13'},
                {url:'',title:'如果你来读一首诗，你会为谁而读14'},
                {url:'',title:'返校后的第一份早餐15'},
                {url:'',title:'大学生创业项目16'},
                {url:'',title:'马冬梅17'},
                {url:'',title:'NM$L18'},
                {url:'',title:'最受感触的一个获奖感言19'},
                {url:'',title:'少年派20'},

            ],
            samples:[
                {con:'为了解压，你做出过哪些奇怪的事情'},{con:'关于宇宙的浪漫句子'},{con:'现实生活中“误入”他人世界的经历'},
                {con:'你是如何对抗人生的荒谬的'},{con:'你日常生活中的小发明'},{con:'你弄丢的最心疼的一本书'},
                {con:'DDL下的人们是怎样活着的'},{con:'村子里的老人'},{con:'游戏是否可以一定程度替代书籍'},
                {con:'那些出国以后才知道的事'},
            ],
            hpn:0,
            st_spots:[]
        }
    },
    methods:{
         jmp(addr){
       this.$router.push({path:addr});
    },
        tohd(hpno){
            this.$router.push({path:'/hd',query:{hpno:hpno}});
        },
        search()
        {
            console.log(this.keyword);
            var pre_spots=this.st_spots
            this.spots=[]
            var i
            for(i=0;i<pre_spots.length;i++){
                if(pre_spots[i].title.includes(this.keyword)){
                    this.spots.push(pre_spots[i])
                }
            }
        },

        redo()
        {
             function resetFun(arr){
                    var len = arr.length,
                        randomIndex,
                        temp;
                    while(len){
                        // 生成一个随机数与数组最后一个元素交换，排除最后一个，再生成一个随机数与倒数第二个交换，依次类推
                        randomIndex = Math.floor(Math.random() * (len--));
                        temp = arr[randomIndex];
                        arr[randomIndex] = arr[length];
                        arr[length] = temp;
                    }
                    return arr;
                }
            this.refresh=false;
            let arr=this.spots;
            this.spots=resetFun(arr);
            this.refresh=true;
             console.log(this.spots);
        }
    },
    created() {
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
        console.log(guro);
        console.log(gurt);
        console.log(this.urid);
        let params={op:1};
        this.$http.post('/gethp.php',params).then(
             response=>{
                 this.spots=[];
                 var hplist=response.data
                 this.hpn=hplist.length
                 var i;
                 for(i=0;i<hplist.length;i++){
                     var s1=hplist[i][0];
                     var s2=hplist[i][1];
                     var s3=hplist[i][2];
                     var tmp={hpno:s1,url:s2,title:s3};
                     this.spots.push(tmp);
                 }
                 this.st_spots=this.spots;
             }
        )
    }
}
</script>

